<template>
    <div class="confirmWrap">
        <header>
            <router-link to="/msite"><i class="el-icon-arrow-left"></i> </router-link>
            <div class="top_title"><span>确认订单</span></div>
            <router-link to="/confirmorder"><i class="el-icon-s-custom"></i> </router-link>
        </header>
        <router-link to="">
            <div class="address_empty_left">
                <div><i class="el-icon-location-outline"></i></div>
                <div class="address_detail_container">
                    <div class="top">
                        <span>{{name}}</span>
                        <span>先生</span>
                        <span>{{phone}}</span>
                    </div>
                    <div class="address_detail">
                        <span>公司</span>
                        <p>{{address}}</p>
                    </div>
                </div>
            </div>
            <div class="address_empty_right">
                <i class="el-icon-arrow-right" @click="gochooseaddress"></i>
            </div>
        </router-link>
        <div class="delivery_model container_style">
            <p>送达时间</p>
            <div class="deliver_time">
                <p>尽快送达 | 预计 {{distObj.delivery_reach_time}}</p>
                <p>蜂鸟专送</p>
            </div>
        </div>
        <div class="pay_way container_style">
            <div class="up">
                <span>支付方式</span>
                <div>
                    <span>在线支付</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </div>
            <div class="down">
                <span>红包</span>
                <span>暂时只在饿了么 APP 中支持</span>
            </div>
        </div>
        <div class="food_list">
            <div class="food_list1">
                <img src="" alt="">
                <span>{{shopName}}</span>
            </div>
            <ul>
                <li v-for="c in carArr" :key="c.name">
                    <p>{{c.name}}{{c.specs == ''?'':'-'+c.specs}}</p>
                    <div>
                        <span>x{{c.num}}</span>
                        <span>¥{{c.price}}</span>
                    </div>
                </li>
            </ul>
            <div class="food_list3">
                <p>餐盒</p>
                <div>
                    <span></span>
                    <span>￥{{canhe}}</span>
                </div>
            </div>
            <div class="food_list3">
                <p>配送费</p>
                <div>
                    <span></span>
                    <span>￥{{peisong}}</span>
                </div>
            </div>
            <div class="food_list3">
                <p>订单￥{{peisong + canhe + +allPrice}}</p>
                <div>
                    <span>待付款￥{{peisong + canhe + +allPrice}}</span>
                </div>
            </div>
        </div>
        <div class="pay_way container_style listthree">
            <div class="up">
                <span>订单备注</span>
                <div>
                    <span>口味、偏好等</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </div>
            <div class="up">
                <span>发票抬头</span>
                <div>
                    <span>不需要开发票</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </div>
        </div>
        <div class="confrim_order">
            <p>待支付 ¥{{peisong + canhe + +allPrice}}</p>
            <p @click="gopayment">确认下单</p>
        </div>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    export default {
        name: "ConfirmOrder",
        data(){
            return{
                username:'',
                placeNameHistory:'',
                geohash:'',
                shopMsg:[],
                shopId:"",
                carArr: [],
                shopCarts: null,
                shopName:'',
                entities:[],
                distObj:{},
                thisAddress:0,
                addressMsg:'',
                isShow:false,
                canhe:'',
                peisong:'',
                allPrice:'',
                zongjia:'',
                userid:'',
                name:'',
                phone:'',
                address:'',
                addresslist:[],
            }
        },
        created(){
            this.allPrice = localStorage.getItem('allprice');
            this.thisAddress = 0;
            this.thisAddress = localStorage.getItem('thisAddress');
            this.shopMsg = JSON.parse(localStorage.getItem('shopId'));
            this.shopName = this.shopMsg.name;
            this.shopId = this.shopMsg.id;
            this.userid = localStorage.getItem('userId');
            this.addresslist = JSON.parse(localStorage.getItem('addresslist'));
            console.log(this.addresslist);
            this.axios.get("/v1/users/"+this.userid+"/addresses",{
                params:{
                    user_id:this.userid,
                }
            }).then(res=>{
                console.log(res)
                this.addressMsg = res;
                // console.log(res[0].name)
                this.name = this.addressMsg[this.thisAddress].name;
                this.phone = this.addressMsg[this.thisAddress].phone;
                this.address = this.addressMsg[this.thisAddress].address;
            }).catch(err=>{})
            this.username = localStorage.getItem('usermsg');
            this.placeNameHistory = localStorage.getItem("placeNameHistory")
            let hisArr = JSON.parse(localStorage.getItem("placeHistory"))
            let newArr = hisArr.filter(v=>{
                return v.name == this.placeNameHistory
            })
            this.geohash = newArr[0].geohash;
            this.$store.commit("init_buy_cart");
            this.carArr = [];
            this.shopCarts = this.$store.state.cartList;
            Object.keys(this.shopCarts).forEach(shopId => {
                Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                    Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                        Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                            let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                            let newFoodItem = {...foodItem, category_id, item_id, food_id, shopId}
                            if (newFoodItem.shopId == this.shopId) {
                                this.carArr.push(newFoodItem);
                            }
                        });
                    })
                })
            })
            for (let i = 0; i < this.carArr.length; i++) {
                let p = [{attrs:[],extra:{},id:this.carArr[i].id,name:this.carArr[i].name,price:this.carArr[i].price,packing_fee:this.carArr[i].packing_fee,quantity:this.carArr[i].num,sku_id:this.carArr[i].sku_id,specs:this.carArr[i].specs,stock:this.carArr[i].stock,}]
                this.entities.push(p)
            }
            this.axios.post("/v1/carts/checkout",{
                restaurant_id:this.shopId,
                geohash:this.geohash,
                entities:this.entities
            }).then(res=>{
                console.log(res)
                this.distObj = res;
                this.canhe = res.cart.extra[0].price;
                this.peisong = res.cart.deliver_amount;
                localStorage.setItem('cartid',res.id)
                // console.log(typeof this.peisong)
            }).catch(err => {})
                },
        methods:{
            gopayment(){
                this.$router.push("/confirmorder/payment")
            },
            gochooseaddress(){
                this.$router.push("/chooseaddress")
            }
        }
    }
</script>

<style scoped>
