        width: 100%;
        height: 1.95rem;
    }
    a{
        text-decoration: none;
    }
    header>a>i{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        line-height: 2.2rem;
        /*margin-left: .4rem;*/
    }
    header>*:first-child{
        float: left;
        margin-left: .4rem;
    }
    header>*:last-child{
        float: right;
        margin-right: .8rem;
    }
    .top_title{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .top_title>span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .confirmWrap>a{
        min-height: 3.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .6rem;
        background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADQAAAAGCAYAAACMyr1NAAAAAXNSR…iSY/y/syxa9jY+tYx//7fikydGDhggKxhdoi4Ro3bYqAEAknE5DXYMR0IAAAAASUVORK5CYII=) 0 100% repeat-x;
        background-color: #fff;
        background-size: auto .12rem;
        margin-top: 1.95rem;
    }
    .address_empty_left{
        display: flex;
        align-items: center;
    }
    .address_empty_left>div>i{
        font-size: .8rem;
        color: #3190e8;
        margin-right: .2rem;
    }
    .address_detail_container{
        margin-left: .2rem;
    }
    .top{
        font-size: .65rem;
        color: #333;
        padding-bottom: .2rem;
    }
    .top>*:first-child{
        font-size: .8rem;
        font-weight: 700;
    }
    .top>span{
        font-size: .65rem;
        color: #333;
    }
    .address_detail{
        width: 100%;
        display: flex;
        align-items: center;
    }
    .address_detail>span{
