    }
    .food_list>ul>li>div>*:first-child{
        color: #f60;
    }
    .food_list3{
        display: flex;
        justify-content: space-between;
        line-height: 1.8rem;
        padding: 0 .7rem;
    }
    .food_list3>p{
        width: 11rem;
    }
    .food_list3>div>span{
